import React, { Component } from 'react';
import TaskList from './components/TaskList';
import AddTask from './components/AddTask';
import Title from './components/Title';
import InitialState from './InitialState';

class App extends Component {
  constructor(props) {
    super(props);
    this.todoList = React.createRef();
    this.state = InitialState;
  }
  getNewID = () => {
    let ids = Array.from(this.state.tasks).map(task => task.id);

    if (ids.length < 1) {
      return 0;
    }

    return (ids.sort((a, b) => a - b).pop()) + 1;
  }
  addTask = (task) => {
    task.id = this.getNewID();
    let tasks = [...this.state.tasks];
    tasks.unshift(task);

    this.setState({ tasks: tasks });
  }
  removeTask = (taskID) => {
    let tasks = Array.from(this.state.tasks);
    tasks = tasks.filter(task => task.id !== Number(taskID));

    this.setState({ tasks: tasks });
  }
  setTaskList = (tasks) => {
    this.setState({ tasks: tasks });
  }
  setTaskDone = (task) => {
    let tasks = [...this.state.tasks];

    // Remove
    let position = tasks.map(task => task.id).indexOf(task.id);
    [task] = tasks.splice(position, 1);

    // Set Done
    task.done = true;

    // Split
    let index = tasks.map(task => task.done).lastIndexOf(false);
    let firstHalf = tasks.slice(0, index + 1);
    let secondHalf = tasks.slice(index + 1);

    // Glue the parts back together
    let editedTask = [];
    editedTask.push(task);
    tasks = firstHalf.concat(editedTask, secondHalf);

    // Push the new state
    this.setState({ tasks: tasks });
  }
  setTaskTodo = (task) => {
    let tasks = [...this.state.tasks];

    // Remove
    let position = tasks.map(task => task.id).indexOf(task.id);
    [task] = tasks.splice(position, 1);

    // Set Done
    task.done = false;

    // Split
    let index = tasks.map(task => task.done).lastIndexOf(false);
    let firstHalf = tasks.slice(0, index + 1);
    let secondHalf = tasks.slice(index + 1);

    // Glue the parts back together
    let editedTask = [];
    editedTask.push(task);
    tasks = firstHalf.concat(editedTask, secondHalf);

    // Push the new state
    this.setState({ tasks: tasks });
  }
  changeTaskCount = (task, newCount) => {
    let position = this.state.tasks.map(task => task.id).indexOf(task.id);
    let tasks = [...this.state.tasks];
    tasks[position].count = newCount;

    this.setState({ tasks: tasks });
  }
  render() {
    return (
      <div className="App">
        <nav id="tasks-panel" className="container">
          <Title text="My personal todo list" />
          <AddTask addTask={this.addTask} />
          <TaskList
            tasks={this.state.tasks}
            removeTask={this.removeTask}
            ref={this.todoList}
            setTaskList={this.setTaskList}
            setTaskDone={this.setTaskDone}
            setTaskTodo={this.setTaskTodo}
            changeTaskCount={this.changeTaskCount} />
        </nav>
      </div>
    );
  }
}

export default App;



